import React, {useEffect} from 'react';
import {gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";

import styles from './Hero.module.scss';
import {HeroTextAnimated} from "../HeroTextAnimated/HeroTextAnimated";

gsap.registerPlugin(ScrollTrigger);

export const Hero = () => {
    useEffect(() => {
        const texts = gsap.utils.toArray('.header-text');
        gsap.to(texts, {
            y: 0,
            opacity: 1,
            delay: 0.3,
            duration: 0.7,
            ease: 'back.out(1.5)',
            stagger: {
                each: 0.1,
            }
        })
    }, []);

    return (
        <div id={'home'} className={styles.wrapper}>
            <div
                id={'header'}
                className={styles.container}
            >
                <div className={styles.textWrapper}>
                    <span className={`${styles.text} header-text`}>Design</span>
                    <span className={`${styles.text} header-text`}>and code</span>
                    <HeroTextAnimated/>
                    <span className={`${styles.text} header-text`}>apps</span>
                </div>
            </div>
        </div>
    );
};